import React, { Component } from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Home from '../home';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';
import Edit from '@material-ui/icons/Edit';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Cancel from '@material-ui/icons/Cancel';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';


const styles = theme => ({
    button: {
        margin: '${theme.spacing.unit * 2}px',
    },
    input: {
        display: 'none',
    },
    paper: {
        padding: '${theme.spacing.unit * 3}px',
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    alignLeft: {
        marginLeft: "10px",
        marginBottom: "10px"
    }
})

class Bank extends Component {
    state = {
        banksDB: [],
        dialog: false
    }

    handleClose(e){
        this.setState({
            dialog: false
        })
    }
    
    delete(value, e) {
        this.handleClose(e);
        // let db = indexedDB.open("MyData");
        // db.onsuccess = (event) => {
        //     let tx = event.target.result.transaction(['banks'], "readwrite");
        //     let store = tx.objectStore('banks');
        //     let selectedItem = store.delete(Number(value));
        //     selectedItem.onsuccess = (event) => {
        //         let selectadd = store.getAll();
        //         selectadd.onsuccess = (event) => {
        //             this.setState({
        //                 banksDB: event.target.result
        //             })
        //         }
        //     }
        // }
    }
    componentDidMount() {
        let db = indexedDB.open("MyData");
        db.onsuccess = (event) => {
            let tx = event.target.result.transaction(['banks'], "readonly");
            let store = tx.objectStore('banks');
            let selectadd = store.getAll();
            selectadd.onsuccess = (event) => {
                this.setState({
                    banksDB: event.target.result
                })
            }
        }
    }

    render() {

        return (
            <div >
                <Home />
                <div className={this.props.classes.alignLeft} color="inherit">
                  <List dense>      {this.state.banksDB.map(item => (
                        <ListItem key={item.bankId} button>       
                        <Link to={'/editbank/' + item.bankId}>                     
                            <ListItemIcon>
                            <Edit />
                            </ListItemIcon>
                         </Link>
                         <ListItemText
                    primary= {item.name}/>
                        <ListItemSecondaryAction>
                        <IconButton onClick={this.delete.bind(this, item.bankId)} edge="end" aria-label="Delete">
                      <DeleteIcon />
                    </IconButton>
                        </ListItemSecondaryAction>
                        </ListItem>
                    ))}
                    </List>


                    <Button variant="outlined" color="primary" component={Link} to="/addbank" className={this.props.classes.button}>
                    Add bank
                </Button>
                </div>
                <Dialog
        open={this.state.dialog}
        onClose={this.handleClose.bind(this)}
        aria-labelledby="draggable-dialog-title"
      >
        <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
          Subscribe
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            To subscribe to this website, please enter your email address here. We will send updates
            occasionally.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose.bind(this)} color="primary">
            Cancel
          </Button>
          <Button onClick={this.handleClose.bind(this)} color="primary">
            Subscribe
          </Button>
        </DialogActions>
      </Dialog>
            </div>

        )
    }
}

Bank.props = {
    classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Bank);